html {
  font-size: 62.5%;
}

img {
  object-fit: contain;
  max-width: 100%;
  height: auto;
  display: block;
}

.toolbar .toolbar-icon:before {
  width: 2rem;
}

.toolbar .toolbar-icon {
  margin: 0;
  font-size: 1.3rem;
}

@media only screen and (min-width: 36em) {
  .toolbar .toolbar-bar .toolbar-tab > .toolbar-icon:before {
    width: 2rem;
  }
}

.toolbar .toolbar-toggle-orientation button {
  width: 3.9rem;
  height: 3.9rem;
}

.dialog-off-canvas-main-canvas {
  grid-template-rows: auto 1fr auto;
  grid-template-columns: minmax(0, 1fr);
  min-height: 100vh;
  display: grid;
}

.dialog-off-canvas-main-canvas * {
  box-sizing: border-box;
}

.toolbar-horizontal .dialog-off-canvas-main-canvas, .toolbar-vertical .dialog-off-canvas-main-canvas {
  min-height: calc(100vh - 39px);
}

.toolbar-horizontal.toolbar-tray-open .dialog-off-canvas-main-canvas {
  min-height: calc(100vh - 79px);
}

header, main, footer {
  background-color: #000;
}

.last-m-0 > :last-child {
  margin: 0;
}

.divider-12 {
  margin: 0 0 1.2rem;
}

.divider-16 {
  margin: 0 0 1.6rem;
}

.divider-24 {
  margin: 0 0 2.4rem;
}

.divider-small {
  margin: 0 0 4rem;
}

.divider-large {
  margin: 0 0 8rem;
}

.margin-auto {
  margin: auto;
}

@media only screen and (max-width: 599px) {
  .divider-large {
    margin: 0 0 4rem;
  }

  .divider-small--mobile-24 {
    margin: 0 0 2.4rem;
  }
}

body {
  font-family: Arial, Helvetica, sans-serif;
}

h1 {
