}

h2 {
  color: #fff;
  margin: 0 0 2.4rem;
  font-size: 4.1rem;
  font-style: normal;
  font-weight: 700;
  line-height: 130%;
}

h3 {
  color: #fff;
  margin: 0 0 2.4rem;
  font-size: 3.6rem;
  font-style: normal;
  font-weight: 700;
  line-height: 130%;
}

h4 {
  color: #fff;
  margin: 0 0 2.4rem;
  font-size: 3rem;
  font-style: normal;
  font-weight: 700;
  line-height: 130%;
}

h5 {
  color: #fff;
  margin: 0 0 2.4rem;
  font-size: 2.5rem;
