}

p, a, ul, ol {
  color: #fff;
  margin: 0 0 2.4rem;
  font-size: 1.6rem;
  font-style: normal;
  font-weight: 400;
  line-height: 160%;
}

a {
  color: #fff;
  margin: 0;
  text-decoration: underline;
}

a:hover {
  text-decoration: none;
}

#toolbar-administration li, .toolbar-menu li, .contextual-links li {
  margin: 0;
  font-size: 1.3rem;
  display: block;
}

#toolbar-administration li:before, .toolbar-menu li:before, .contextual-links li:before {
  content: none;
}

#toolbar-administration a, .toolbar-menu a {
  font-size: 1.3rem;
}

.grey-bg {
  background-color: #dddfe0;
}

.audio-sample--teaser {
  color: #fff;
  justify-content: space-between;
  align-items: center;
  gap: 2rem;
  display: flex;
}

.audio-sample--teaser .label p {
  border: 1px solid #fff;
  margin: 0;
  padding: .5rem 2rem;
}

@media only screen and (max-width: 767px) {
  .audio-sample--teaser {
    flex-flow: column;
  }
}

.audio-samples__overview {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

@media only screen and (max-width: 767px) {
  .audio-samples__overview {
    margin: 0 auto;
  }
}

.awesome-pager ul {
  justify-content: center;
  gap: 2rem;
  padding: 0;
  display: flex;
}

.awesome-pager ul li::marker {
  content: none;
}

.button {
  cursor: pointer;
  border-radius: .4rem;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  padding: 1.2rem 1.6rem;
}

.button a {
  text-decoration: none;
}

.button:hover {
  box-shadow: 0 3px 8px #00758659;
}

.contact {
  background-image: url("/themes/custom/awesome/dist/images/background-microphone.jpeg");
  background-position: center;
  background-repeat: no-repeat;
}

.two-col {
  grid-template-columns: repeat(2, 1fr);
  gap: 4rem;
  display: grid;
}

.contact__content {
  gap: 2rem;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 2rem;
  display: grid;
}

.contact__content > div:first-of-type {
  border-bottom: 1px solid #fff;
}

.contact__content .first, .contact__content .second {
  gap: 2rem;
  padding-bottom: 2rem;
  display: flex;
}

.contact__content .photo {
  max-width: 12rem;
}

.hamburger {
  display: none;
}

@media only screen and (max-width: 767px) {
  .hamburger {
    cursor: pointer;
    background-color: #fff;
    border-radius: .4rem;
    place-content: center;
    justify-self: end;
    height: 100%;
    padding: 1.2rem 1.6rem;
    display: grid;
  }

  .hamburger .bar {
    background-color: #000;
    width: 1.6rem;
    height: 2px;
    margin: 2px auto;
    transition: all .3s ease-in-out;
    display: block;
  }

  .hamburger.is-active span:first-child {
    transform: translateY(7px)rotate(45deg);
  }

  .hamburger.is-active span:nth-child(2) {
    opacity: 0;
  }

  .hamburger.is-active span:nth-child(3) {
    transform: translateY(-5px)rotate(-45deg);
  }
}

.hero-banner__content img {
  z-index: -2;
  filter: brightness(.8);
}

.header__navigation {
  z-index: 2;
  grid-template-columns: 20% auto;
  align-items: center;
  padding: 2rem 0;
  display: grid;
  position: relative;
}

.menu {
  padding: 1rem;
}

.main-menu {
  grid-auto-flow: column;
  gap: 2rem;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: 0 auto;
  padding: 0;
  list-style: none;
  display: grid;
}

.main-menu__li {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.main-menu__li a {
  color: #fff;
  text-underline-offset: 3px;
  font-size: 2rem;
}

@media only screen and (max-width: 767px) {
  .header__navigation {
    grid-template-columns: auto auto;
  }

  .main-menu {
    display: none;
  }

  .main-menu.is-active {
    z-index: 1;
    background-color: #000;
    flex-flow: column;
    align-items: flex-end;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: 100vh;
    padding: 0 6rem;
    display: flex;
    position: absolute;
    top: 10rem;
    right: 0;
  }
}

.photoswipe-gallery {
  grid-template-columns: repeat(4, 1fr);
  place-items: center;
  gap: 3rem;
  display: grid;
}

.photoswipe-gallery a {
  filter: brightness(.8);
  transition: all .3s ease-in-out;
  position: relative;
}

.photoswipe-gallery a img {
  border-radius: 10px;
}

.photoswipe-gallery a:after {
  content: "";
  background-image: url("/themes/custom/awesome/dist/images/photo-focus.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 100%;
  height: 100%;
  display: none;
  position: absolute;
  top: 0;
}

.photoswipe-gallery a:hover {
  filter: brightness();
}

.photoswipe-gallery a:hover:after {
  display: block;
}

@media only screen and (max-width: 767px) {
  .photoswipe-gallery {
    grid-template-columns: 1fr;
  }
}

.repertoar-background {
  position: relative;
}

.repertoar-background img {
  opacity: .8;
  width: 100%;
}

.repertoar-overview {
  position: relative;
}

.repertoar-overview__content {
  background-color: #000;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  box-shadow: 0 0 27px 29px #000;
}

.repertoar-overview__content h2 {
  text-align: center;
}

@media only screen and (max-width: 767px) {
  .repertoar-overview__content {
    width: 100%;
  }

  .repertoar-overview__content h2 {
    margin: 0;
  }
}

.samples-view {
  gap: 2rem;
  display: grid;
}

.song-teaser__content {
  color: #fff;
  white-space: nowrap;
  gap: 1rem;
  display: flex;
}

.song-teaser__content p {
  margin: 0;
}

.song-teaser__content .interpret {
  font-size: max(1.2rem, min(.454545vw + 1.36364rem, 2rem));
  font-weight: 700;
}

.song-teaser__content .label {
  font-size: max(1.2rem, min(.454545vw + 1.36364rem, 2rem));
  font-style: italic;
}

.song-teaser__content > div {
  font-size: 2rem;
}

@media only screen and (max-width: 767px) {
  .song-teaser__content .label {
    white-space: normal;
  }
}

.two-col-wrapper {
  background-image: url("/themes/custom/awesome/dist/images/background-guitar-3.jpeg");
  background-position: center;
  background-repeat: no-repeat;
  position: relative;
}

.two-col-wrapper__content {
  grid-template-columns: 60% 40%;
  gap: 4rem;
  padding: 2rem;
  display: grid;
}

.two-col-wrapper__left p {
  color: #fff;
  z-index: 1;
  border: 2px solid #fff;
  border-radius: 20px;
  padding: 2rem;
  font-size: 2rem;
  position: relative;
}

.two-col-wrapper__left p:before {
  content: "";
  z-index: -1;
  background-color: #00000080;
  border-radius: 20px;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.two-col-wrapper--right {
  position: relative;
}

.two-col-wrapper--right .text {
  color: #fff;
  z-index: 1;
  padding: 2rem;
  position: relative;
}

.two-col-wrapper--right .text img:not(:last-child) {
  margin-bottom: 3rem;
}

.two-col-wrapper--right .text:before {
  content: "";
  z-index: -1;
  background-color: #00000080;
  border-radius: 10px;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.overlay {
  opacity: .5;
  background-color: #000;
  border-radius: 20px;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

@media only screen and (max-width: 767px) {
  .two-col-wrapper__content {
    grid-template-columns: 1fr;
  }
}

.two-col__content {
  grid-template-columns: 50% 50%;
  padding: 1rem;
  display: grid;
}

@media only screen and (max-width: 767px) {
  .two-col__content {
    grid-template-columns: 1fr;
  }

  .two-col__content .col:first-of-type {
    gap: 4rem;
  }
}

.view-repertoar-items {
  background: #000;
  grid-template-columns: 1fr 1fr;
  column-gap: 15rem;
  padding: 2rem;
  display: grid;
  position: relative;
}

.view-repertoar-items:after {
  content: "";
  background-color: #fff;
  width: 2px;
  height: 81%;
  position: absolute;
  top: 50%;
  bottom: 0;
  left: 50%;
  transform: translate(-50%, -50%);
}

@media only screen and (max-width: 767px) {
  .view-repertoar-items {
    grid-template-columns: 1fr;
  }

  .view-repertoar-items:after {
    content: none;
  }
}

.article-3 {
  grid-template-columns: repeat(3, 1fr);
  gap: 2.4rem;
  display: grid;
}

.article-3--same-row-h {
  grid-auto-rows: 1fr;
}

.article-3--news {
